const string = `
.skin * {
    box-sizing: border-box;
}

.skin * {
    margin: 0;
    padding: 0;
}

.skin *::after {
    box-sizing: border-box;
}

.skin *::before {
    box-sizing: border-box;
}
.skin {
    position: relative;
    background-color: #ffe600;
    min-height: 50vh;

}

.nose {
    border: 10px solid black;
    border-color: black transparent transparent;
    border-bottom: none;
    width: 0px;
    height: 0px;
    position: absolute;
    left: 50%;
    top: 145px;
    margin-left: -10px;
    z-index: 10;
}

@keyframes wave {
    0% {
        transform: rotate(0deg);
    }

    33% {
        transform: rotate(5deg);
    }

    66% {
        transform: rotate(-5deg);
    }

    100% {
        transform: rotate(0deg);
    }
}

.nose:hover {
    transform-origin: center bottom;
    animation: wave 300ms infinite linear;
}

.yuan {
    position: absolute;
    left: -10px;
    top: -16px;
    width: 20px;
    height: 6px;
    border-radius: 10px 10px 0 0;
    background-color: black;
}

.eye {
    border: 2px solid #000;
    position: absolute;
    width: 64px;
    height: 64px;
    left: 50%;
    top: 100px;
    margin-left: -32px;
    background-color: #2e2e2e;
    border-radius: 50%;
}

.eye::before {
    content: '';
    display: block;
    border: 3px solid #000;
    width: 28px;
    height: 28px;
    border-radius: 50%;
    background-color: #fff;
    position: relative;
    left: 8px;
    top: 2px;
}

.eye.left {
    transform: translateX(-100px);
}

.eye.right {
    transform: translateX(100px);
}

.mouth {
    width: 200px;
    height: 200px;
    position: absolute;
    left: 50%;
    top: 170px;
    margin-left: -100px;
}

.mouth .up {
    position: relative;
    top: -20px;
    z-index: 1;
}

.mouth .up .lip {
    border: 3px solid black;
    height: 30px;
    width: 100px;
    background-color: #ffe600;
    border-top-color: transparent;
    border-right-color: transparent;
    position: relative;
    position: absolute;
    left: 50%;
    margin-left: -50px;
}

.mouth .up .lip.left {
    border-radius: 0 0 0 50px;
    transform: rotate(-15deg) translateX(-53px);
}

.mouth .up .lip.right {
    border-radius: 0 0 50px 0;
    transform: rotate(15deg) translateX(53px);
}

.mouth .up .lip::before {
    content: '';
    position: absolute;
    display: block;
    width: 7px;
    height: 30px;
    bottom: 0;
    background-color: #ffe600;
}

.mouth .up .lip.left::before {
    right: -6px;
}

.mouth .up .lip.right::before {
    left: -6px;
}

.mouth .down {
    position: absolute;
    top: 5px;
    width: 100%;
    height: 180px;
    overflow: hidden;
}

.mouth .down .yuan1 {
    border: 3px solid black;
    width: 150px;
    height: 1000px;
    position: absolute;
    bottom: 0;
    left: 50%;
    margin-left: -75px;
    border-radius: 75px/300px;
    background-color: #9b000a;
    overflow: hidden;
}

.mouth .down .yuan1 .yuan2 {
    width: 200px;
    height: 300px;
    background-color: #ff485f;
    position: absolute;
    bottom: -155px;
    left: 50%;
    margin-left: -100px;
    border-radius: 100px;
}

.face {
    position: absolute;
    left: 50%;
    margin-left: -44px;
    top: 200px;
    width: 88px;
    height: 88px;
    border: 3px solid black;
    z-index: 3;
}

.face>img {
    position: absolute;
    top: 50%;
    left: 50%;
}

.face.left {
    transform: translateX(-180px);
    background-color: #ff0000;
    border-radius: 50%;
}

.face.left>img {
    transform: rotateY(180deg);
    transform-origin: 0 0;
}

.face.right {
    transform: translateX(180px);
    background-color: #ff0000;
    border-radius: 50%;
}
`
const demo = document.querySelector('#demo')
const demo2 = document.querySelector('#demo2')



const player = {
    id: undefined,
    time: 100,
    n: 1,
    init: () => {
        demo.innerText = string.substr(0, player.n)
        demo2.innerHTML = string.substr(0, player.n)
        player.bindEvents()
        player.play()
    },
    events: {
        '#btnPause': 'pause',
        '#btnPlay': 'play',
        '#btnSlow': 'slow',
        '#btnNormal': 'normal',
        '#btnFast': 'fast'
    },
    bindEvents: () => {
        for (let key in player.events) {
            if (player.events.hasOwnProperty(key)) {
                const value = player.events[key]
                document.querySelector(key).onclick = player[value]
            }
        }
    },
    run: () => {
        player.n += 1
        if (player.n > string.length) {
            window.clearInterval(player.id)
            return
        }
        demo.innerText = string.substr(0, player.n)
        demo2.innerHTML = string.substr(0, player.n)
        demo.scrollTop = demo.scrollHeight
    },
    play: () => {
        player.id = setInterval(player.run, player.time)
    },
    pause: () => {
        window.clearInterval(player.id)
    },
    slow: () => {
        player.pause()
        player.time = 200
        player.play()
    },
    normal: () => {
        player.pause()
        player.time = 50
        player.play()
    },
    fast: () => {
        player.pause()
        player.time = 0
        player.play()
    }

}
player.init()
